import { NgModule }      from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { FormsModule }   from '@angular/forms'; // <-- NgModel lives here

import { HttpModule }    from '@angular/http';

import { AppComponent }  from './app.component';
import { DashboardComponent } from './dashboard.component';
import { HeroDetailComponent } from './hero-detail.component';
import { HeroesComponent } from './heroes.component';
import { HeroSearchComponent } from './hero-search.component';
import { HeroService } from './hero.service';

//import { RouterModule }   from '@angular/router';
import { AppRoutingModule }     from './app-routing.module';



// Imports for loading & configuring the in-memory web api
import { InMemoryWebApiModule } from 'angular-in-memory-web-api';   //Need to run command 'npm i angular-in-memory-web-api' in folder
import { InMemoryDataService }  from './in-memory-data.service';



@NgModule
(
    {
        imports:
        [
            BrowserModule,
            FormsModule, // <-- import the FormsModule before binding with [(ngModel)],
        //    RouterModule.forRoot([
        //        {
        //            path: 'dashboard',
        //            component: DashboardComponent
        //        },
        //        {
        //            path: 'heroes',             //Associates the part of the URL after the base
        //            component: HeroesComponent  //with the HeroesComponent or heroes.component.ts
        //        },
        //        {
        //            path: '',
        //            redirectTo: '/dashboard',
        //            pathMatch: 'full'
        //        },
        //        {
        //            path: 'detail/:id',
        //            component: HeroDetailComponent
        //        }
        //    ])
            HttpModule,

            InMemoryWebApiModule.forRoot(InMemoryDataService),

            AppRoutingModule
        ],

        declarations:
        [
            AppComponent,
            DashboardComponent,
            HeroDetailComponent,
            HeroesComponent,
            HeroSearchComponent
        ],
        providers:
        [
            HeroService
        ]
        ,
        bootstrap:
        [
            AppComponent
        ]
    }
)

export class AppModule
{
}




