import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Hero } from './hero';
import { HeroService } from './hero.service';


@Component({
    selector: 'my-heroes',
    templateUrl: './heroes.component.html',
    styleUrls: [`./heroes.component.css`],
    providers: [HeroService]
})


export class HeroesComponent implements OnInit
{
    heroes: Hero[];
    selectedHero: Hero;

    //The 'heroService' parameter simultaneously defines a private heroService property and identifies it as a
    //HeroService injection site:
    constructor(private router: Router, private heroService: HeroService)
    {
    }
    //Now Angular knows to supply an instance of the HeroService when it creates an AppComponent

    getHeroes(): void
    {
        //this.heroes = this.heroService.getHeroes();
        this.heroService.getHeroes().then(heroes => this.heroes = heroes);
        //this.heroService.getHeroesSlowly().then(heroes => this.heroes = heroes);
    }

    ngOnInit(): void
    {
        this.getHeroes();
    }

    onSelect(hero: Hero): void {
        this.selectedHero = hero;
    }

    gotoDetail(): void
    {
        this.router.navigate(['/detail', this.selectedHero.id]);
    }

    add(name: string): void
    {
        name = name.trim();

        if (!name)
        {
            return;
        }

        this.heroService.create(name)
            .then(hero => {
                this.heroes.push(hero);
                this.selectedHero = null;
            });
    }

    delete(hero: Hero): void
    {
        this.heroService
            .delete(hero.id)
            .then(() => {
                this.heroes = this.heroes.filter(h => h !== hero);
                if (this.selectedHero === hero) { this.selectedHero = null; }
            });
    }

}



